.container {
  margin: 200px 50px 50px;
  display: flex;
}

.content {
  width: 155px;
  border: 1px solid #000;
  margin: 0 auto;
  box-shadow: 5px 10px 5px rgba(0, 0, 0, .5);
}

.item {
  width: 50px;
  height: 50px;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
  border: 1px solid #000;
  font-size: 25px;
  line-height: 50px;
  transition: background .1s;
  display: inline-block;
}

.item:hover {
  background-color: rgba(241, 241, 241, .047);
}

.mbox {
  width: 200px;
  height: 200px;
  text-align: center;
  background-color: rgba(86, 109, 1, .82);
  border: 1px solid #2e2e01;
  justify-content: center;
  align-items: center;
  padding: 30px;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.headText {
  color: #fff;
  text-shadow: 5px 10px 5px rgba(0, 0, 0, .5);
}

